"use strict";

import ContainerNamespace from '../../processor/namespaces/ContainerNamespace';
import FrameNamespace from '../../processor/namespaces/V8FrameNamespace';
import JSUtilsNamespace from '../../processor/namespaces/JSUtilsNamespace';

export default class ActionRunProcessor {

    constructor(configuration, factory) {
        this.processor = factory.getProcessor(configuration['operations'], factory);

        if (configuration['post_operations'] && configuration['post_operations'].length > 0) {
            this.postProcessor = factory.get(configuration['post_operations'], factory);
        }
    }

    execute(augId, containerNamespace, output, userWarnings) {
        this.processor.process(containerNamespace, userWarnings);
        output.sendUserMessage(augId, containerNamespace.readAttribute('store'));

        if (this.postProcessor) {
            output.flushMessages();
            self.postProcessor(containerNamespace);
        }
    }

}
